import React from 'react';  
import ReactDOM from 'react-dom';  

import {
  HashRouter as Router,
  Route,
  Switch
  } from 'react-router-dom';
import MessengerCustomerChat from 'react-messenger-customer-chat';

import Error from './Error';
import './index.css';  
import App from './App';  
import TandC from './TandC';
import {Helmet} from 'react-helmet';
import PatientCare from './PatientCare';
import Privacy from './Privacy';
import Intro from './Intro';
import About from './about'  
import Contact from './contact'  
import CallbackSection from './CallbackSection';
import Navbar from './Navbar'
import Footer from './footer';
import HomeNursing from './HomeNursing';
import BabyCare from './BabyCare';
const url = "https://0ikvfw3bp8.execute-api.ap-south-1.amazonaws.com/dev"
export default class Index extends React.Component{
  constructor(props){
    super(props);
    this.state = {
        curY : 0,
        isMobile : false,
        dropList : false,
        width : 1024,
        navbarState : {
          displayList:false,
          displayMenu:false
        }
        
    }
  }
  scrollToTop=()=>{
    this.setState(state=>({
      curY:state.curY,
      isMobile:state.isMobile,
      dropList:state.dropList,
      width:state.width,
      navbarState:{
          displayList : false,
          displayMenu:false
      }
    }))
    var currentYOffset = window.self.pageYOffset;
    var initYOffset = currentYOffset;

    var intervalId  = setInterval(function(){
      currentYOffset -= 10000000;
      document.body.scrollTop = currentYOffset;
      document.documentElement.scrollTop = currentYOffset;
      if(window.self.pageYOffset<=0){
        clearInterval(intervalId)
      }
    },1)
  }
  toggleDropList= ()=>{
    //console.log(this.state.dropList);
    this.setState(state=>({
      curY : state.curY,
      isMobile:state.isMobile,
      dropList: !state.dropList,
      width:window.innerWidth,
      navbarState:{
        displayList :!state.navbarState.displayList,
        displayMenu:false
      }
    }))

  }
  toggleNavList=()=>{
    this.setState(state=>({
      curY:state.curY,
      isMobile:state.isMobile,
      dropList:state.dropList,
      width:state.width,
      navbarState:{
          displayList :state.navbarState.displayList,
          displayMenu:!state.navbarState.displayMenu
      }
    }))
  }
  componentDidMount() {
    if (typeof window !== "undefined") {
      this.setState({curY:window.pageYOffset,isMobile:window.innerWidth<=770,width:window.innerWidth});
      

      window.onscroll = () => {
        let currentScrollPos = window.pageYOffset;
        // let maxScroll = document.body.scrollHeight - window.innerHeight;
        // //console.log(currentScrollPos)
        // //if (currentScrollPos > 45 && currentScrollPos < maxScroll) {
        //   
        this.setState({curY : currentScrollPos,isMobile : window.innerWidth<=770,width:window.innerWidth});
        
      }
      window.onresize = () =>{
        
        //console.log(window.innerWidth)
        this.setState({curY : window.pageYOffset,isMobile:window.innerWidth<=770,width:window.innerWidth})
      }

    }
  }
render(){
return (  

  <Router> 
     
    <div>  
      <Helmet>
          <title>The Safecure</title>
          
          
          
        </Helmet> 
        
      <Route path="/" render={()=><Navbar scrollToTop={this.scrollToTop} 
                  displayList={this.state.navbarState.displayList} 
                  displayMenu={this.state.navbarState.displayMenu} 
                  toggleNavList={this.toggleNavList} 
                  isMobile = {this.state.isMobile} 
                  curY = {this.state.curY} 
                  handleChange = {this.toggleDropList} />} />  
      
      <Switch>
        <Route exact path ="/" render = {()=><div><Intro scrollToTop={this.scrollToTop} dropList = {this.state.dropList} isMobile = {this.state.isMobile}/><App scrollToTop={this.scrollToTop}url={url}/></div>} />
        
        <Route exact path="/about" render={()=><About scrollToTop={this.scrollToTop}/>} />  
        <Route exact path="/contact" render={()=><Contact width={this.state.width}url={url} scrollToTop={this.scrollToTop}/>} />
        <Route exact path ="/privacy-policy" render={()=><Privacy scrollToTop={this.scrollToTop}/>}/>
        <Route exact path ="/services/patient-care" render = {()=><PatientCare scrollToTop={this.scrollToTop}/>}/>
        <Route exact path ="/services/home-nursing" render= {()=><HomeNursing scrollToTop={this.scrollToTop}/>}/>
        <Route exact path ="/terms-and-conditions" render={()=><TandC scrollToTop={this.scrollToTop}/>}/>
        <Route exact path ="/services/new-born-baby-care" render={()=><BabyCare scrollToTop={this.scrollToTop}/> }/>
        <Route render={()=><Error scrollToTop={this.scrollToTop}/>}></Route> // Error Page
      </Switch>
      <MessengerCustomerChat
    pageId="107424694935896"
    appId="1975739425925754"
    
  />
      <Route path="/" render={()=><CallbackSection scrollToTop={this.scrollToTop}url={url}/>}/>
      <Route path="/" render={()=><Footer scrollToTop={this.scrollToTop}/>} />  
      
    </div>  
  </Router>  
)  
}}
ReactDOM.render(<Index/>, document.getElementById('root'));