import React from 'react'
import Form from './Form'
import Request from './Request'
import 'bootstrap/dist/css/bootstrap.css';

export default class FormSection extends React.Component{
    constructor(props){
        super(props);
        this.state={
            data:[{
                heading: "All-in-one Healthcare",
                data : "Get expert medical care at home from a service provider trusted by many families for our passion towards caring",
                items : ["Registered, Well Trained Nurse.","Qualified, Mannered, Professional Staff","On-time fulfilment.","Prompt, satisfactory helpline."],
                buttonText: "Submit a requirement",
                bgcolor: 'black',
                color : 'white',
                btncolor:'blue'   },
                
                {
                heading: "Emergency Medical Services",
                data : "Get expert medical care at home from a service provider trusted by many families for our passion towards caring",
                items : ["Dressing and Catheterization.","Vigo and Injections.","Ryle's Tube (RL)","Sample Collection."],
                buttonText: "Enquire",
                bgcolor: 'blue',
                color:'white',
                btncolor:'blue'   },
                
            ]
                
        }
    }
    render(){
        return (
            <section className="py-3 px-5 py-md-4 py-lg-5" style={{marginLeft : "2.5%",marginRight:"2.5%"}}>
            <div className = "row">
                <div className="col-lg-6 card ">
                    <Form url={this.props.url}/>
                </div>
                <div className="col-lg-6"  >
                    <Request  data = {this.state.data[0]}/>
                    <Request  data = {this.state.data[1]}/>
                </div>
            </div>
            </section>
        )
    }
}